export class BinaryHelper {

    public static checkBinaryFile(extensionOfFile: string) {
        const binaryFileTypesDict: Record<string,boolean> = {"entity": false, "qjson":false, "process":false,"bpmn":false, "flow":false, "gif": true, "png": true, "jpeg": true, "jpg": true, "svg": true, "woff": true, "woff2": true, "ttf": true, "otf": true};
        return binaryFileTypesDict[extensionOfFile];
      }

    public static getBase64FileContent(fileContent: string){
        const indexOfComma = fileContent.indexOf(',');
        return fileContent.substring(indexOfComma+1);
    }

    public static getBinResource(base64FileContent: string){
        return Buffer.from(base64FileContent, 'base64');
    }

}